body {
    font-family: $main-font;
    font-family: $secondary-font;
    }

h1,h2,h3,h4,h5,h6,p {
    padding: 0;
    margin: 0;
    }
    
    img {
    display: block;
    max-width: 100%;
    height: auto;
    width: 100%;
    }
    
    .list {
    padding: 0;
    margin: 0;
    list-style: none;
    }
    
    .link {
    text-decoration: none;
    }

    .no-scroll {
        overflow: hidden;
    }